.container {
    display: grid;
    grid-template-columns: 1fr 6fr 2fr;
    border: 1px solid #ccc;
    align-items: center;
}

.drag {
    align-self: stretch;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: move;
}

.title {
    padding: 20px;
    margin: 0;
    border-right: 1px solid #ccc;
    border-left: 1px solid #ccc;
    cursor: pointer;
}

.actions {
    display: flex;
    justify-content: space-evenly;
}

.completed {
    opacity: 0.5;
}

.completed .title {
    font-style: italic;
    text-decoration: line-through;
}

.input {
    width: 100%;
    margin-bottom: 24px;
}

.modalActions {
    display: flex;
    justify-content: space-between;
}

.modalButton {
    width: 45%;
}

.todoItemButton {
    padding: 10px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
}
